import React from 'react';
import ReactMapGL, { Marker, Popup } from 'react-map-gl';

const Map = ({ caseStudies }) => {
  const [viewport, setViewport] = React.useState({
    latitude: 20,
    longitude: 0,
    zoom: 2,
    bearing: 0,
    pitch: 0,
    width: '100vw',
    height: '100vh'
  });

  const [selectedStudy, setSelectedStudy] = React.useState(null);

  return (
    <ReactMapGL
      {...viewport}
      mapboxApiAccessToken={process.env.MAPBOX_API_KEY}
      onViewportChange={viewport => setViewport(viewport)}
    >
      {caseStudies.map(study => (
        <Marker key={study.id} latitude={study.lat} longitude={study.lng}>
          <div onClick={() => setSelectedStudy(study)}>
            <img src="/marker-icon.png" alt="Marker" />
          </div>
        </Marker>
      ))}

      {selectedStudy && (
        <Popup
          latitude={selectedStudy.lat}
          longitude={selectedStudy.lng}
          onClose={() => setSelectedStudy(null)}
        >
          <div>
            <h2>{selectedStudy.name}</h2>
            <p>{selectedStudy.shortDescription}</p>
            <a href={`/case-studies/${selectedStudy.id}`}>Learn more</a>
          </div>
        </Popup>
      )}
    </ReactMapGL>
  );
};

export default Map;
